.features {
  position: relative;
  min-height: 420px;
  padding: 0 20px 16px 20px;
  background-color: $basic-white;

  @media (min-width: $tablet) {
    padding: 0 100px 0 30px;
  }

  @media (min-width: $tablet) and (max-width: 1439px) {
    &::after {
      position: absolute;
      bottom: 16px;
      left: 0;
      width: 100%;
      height: 1px;
      content: "";
      background-color: $header-border;
    }
  }

  @media (min-width: $desktop) {
    @include container;
    padding: 0 110px;
  }
}

.features__title {
  font-size: 36px;
  font-weight: inherit;
  line-height: 1.1;
  color: $basic-black;

  @media (min-width: $tablet) {
    margin-top: 45px;
    font-size: 60px;
    line-height: 1;
  }

  @media (min-width: $desktop) {
    margin-top: 72px;
  }
}

.features__list {
  @include reset-list-style;
  margin-top: 42px;

  @media (min-width: $tablet) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 130px;
  }

  @media (min-width: $desktop) {
    margin-top: 53px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-column-gap: 80px;
  }
}

.features__item {
  position: relative;
  margin-bottom: 27px;
  margin-left: 80px;
  font-family: $basic-font-family;
  line-height: 1.3;
  color: $basic-grey-text;

  &::before {
    position: absolute;
    top: -4px;
    left: -80px;
    width: 60px;
    height: 60px;
    content: "";
    background-color: $basic-green;
    background-repeat: no-repeat;
    background-position: center;
  }

  &--nutrition::before {
    background-image: url("../img/svg/index-icon-leaf.svg");
    background-size: 30px 32px;
  }

  &--water::before {
    background-image: url("../img/svg/index-icon-tub.svg");
    background-size: 29px 31px;
  }

  &--food::before {
    background-image: url("../img/svg/index-icon-eat.svg");
    background-size: 25px 30px;
  }

  &--time::before {
    background-image: url("../img/svg/index-icon-alarm.svg");
    background-size: 32px 33px;
  }

  @media (min-width: $tablet) {
    width: 100%;
    min-height: 205px;
    margin-top: 115px;
    margin-bottom: 0;
    margin-left: 0;
    font-size: 16px;
    line-height: 1.5;

    &::before {
      top: -95px;
      left: 0;
      width: 80px;
      height: 80px;
    }

    &::after {
      position: absolute;
      top: -100px;
      left: 125px;
      width: 134px;
      height: 280px;
      content: "";
      background-repeat: no-repeat;
    }

    &--nutrition::after {
      width: 112px;
      background-image: url("../img/svg/index-icon-one.svg");
    }

    &--water::after {
      background-image: url("../img/svg/index-icon-two.svg");
    }

    &--food::after {
      background-image: url("../img/svg/index-icon-three.svg");
    }

    &--time::after {
      width: 136px;
      background-image: url("../img/svg/index-icon-four.svg");
    }
  }
}

.features__text {
  position: relative;
  z-index: 10;
}
